body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Backdrop {
  position: absolute;
  z-index: 0;
  height: 100vh;
  width: 100%;
  background: url(/static/media/background.c3ddf076.png) no-repeat fixed;
  background-size: cover;
  background-position: center;
}
.NavHeader {
  z-index: 10;
  width: 100%;
  height: 4.2em;
  position: fixed;
  top: 0;
  background: url(/static/media/background.c3ddf076.png) no-repeat fixed;
}


.Header {
  margin-top: 0.3em;
  z-index: 10;
  width: 100%;
}




.Header .NavBk {
  border-radius: 4px;
}

.Header a {
  margin-left: .7em;
  font-weight: 700
}

.Header .addBtn,
.Header button[class='navbar-toggler'] {
  margin-right: 0.8em;
  color: black;
}

.Navs{
  position: absolute;
  right: 1em;
  float: right;
}

.Header .addBtn input,
.Header .addBtn button {
  font-size: 0.91em
}

.Header nav button[class*='add'] {
  width: 6em
}


.row{
  margin-top: 4.12em;

}
.Col {
  margin-bottom: 0.5em;
  /* padding: 0.5em */
}

.Empty{
  margin-top: 15em;
  text-align: center;
  color: rgb(205,205,205);
  background: none;
  border: none;
  box-shadow: none;
}
.Empty h5{
  font-size: 30px;
}

/* List */
.List{
  background: rgb(240,240,240, 0.95);
}
.List .card-body{
  padding: 0.5em
}
.List button[class*='close']{
  margin: -0.5em -0.2em;
  font-size: 1.4em;
  color: rgb(150,150,150)
}
.List button[class*='close'] span:hover {
  color: red;
  cursor: pointer;
}
.List .DashHeader,
.List .ListSum {
  width: 80%;
  float: left;
  height: 3em;
}
.DashHeader h5,
.ListSum h5{
  font-weight: 700;
  height: 1.4em;
  margin: 0;
  /* background: red */
}


.DashHeader h5:hover {
  color: rgb(73, 19, 104);
  cursor: pointer;
  -webkit-animation: enable 0.1s linear;
          animation: enable 0.1s linear;
}
@-webkit-keyframes enable {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  60% {
    -webkit-transform: scale(1.01);
            transform: scale(1.01);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes enable {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  60% {
    -webkit-transform: scale(1.01);
            transform: scale(1.01);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}




.DashHeader span,
.ListSum span{
  position: relative;
  top: -0.5em;
  height: 0.5em;
  font-size: 0.8em;
  font-weight: 600;
  color: rgb(90,90,90)
}
.DashHeader span i{
  font-size: 0.9em;
}
.List .ListSum{
  text-align: center;
  width: 20%;
  float: right;
}


/* DashInput */
.DashInput input{
  font-size: 0.9em
}


/* ListGroup */
.ListGroup{
  margin-top: 0.2em;
}
.ListGroup li {
  padding: 0.5em 0.5em;
  margin-bottom: 0.1em
}
.ListGroup .DashInput {
  margin-bottom: 0.1em
}
.ListGroup li p {
  font-size: 0.95em;
  font-weight: 500;
  word-spacing: 0.1em;
  width: 100%;
  line-height: 1;  
}

.ListGroup li p[class*='done'] {
  text-decoration: line-through;
  color:red
}
.ListGroup li p:hover {
  color: rgb(73,19,104);
  cursor: pointer;
  -webkit-animation: enable 0.1s linear;
          animation: enable 0.1s linear;
}
.ListGroup .Icons {
  width: 100%;
}
.ListGroup .Icons small {
  width: 80%;
  font-size: 0.68em;
  padding-left: 1em;
  font-style: italic;
}
.ListGroup .Icons i {
  width: 20%;
  text-align: right;
  padding-right: 0.6em;
  color: rgb(200,200,200)
}
.ListGroup .Icons i:hover {
  color: red;
  cursor: pointer;
  -webkit-animation: grow 0.1s linear;
          animation: grow 0.1s linear;
}

.None {
  text-align: center;
  font-size: 1.9em;
  margin: 0;
  font-weight: 500;
  color: rgb(190,190,190)
}


@-webkit-keyframes grow {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  60% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}


@keyframes grow {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  60% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.Login {
  background: rgb(240, 240, 240, 0.95);
  padding: 1em;
  margin-top: 10em;
  border-radius: 4px
}

.Login h5 {
  text-align: center;
  font-size: 1.5em
}

.Login input,
.Login button {
  font-size: 0.95em;
}

.Login small {
  font-size: 0.6em;
  font-weight: 600;
  color: red !important;
  padding-left: 0.5em;
  text-align: center
}
.Login small[class*='text-success'] {
  color: green !important;
}
.Signup {
  background: rgb(240, 240, 240, 0.95);
  padding: 1em;
  margin-top: 10em;
  border-radius: 4px
}

.Signup h5 {
  text-align: center;
  font-size: 1.5em
}

.Signup input,
.Signup button {
  font-size: 0.95em;
}

.Signup small{
  font-size: 0.6em;
  font-weight: 600;
  color: red !important;
  padding-left: 0.5em;
  text-align: center
}

.jumbotron{
  position: relative;
  z-index: 10;
  margin-top: 11em
}

.lead{
  margin-top: 1em
}

.leadBtn{
  margin-top: 2em
}
.leadBtn a:nth-child(1){
  width: 6.5em;
  margin-left: 2em
}
.leadBtn a:nth-child(2) {
  width: 6.5em;
  margin-right: 2em
}
.App {
  position: absolute;
  width: 100%;
  background: url(/static/media/background.c3ddf076.png) no-repeat fixed;
  background-size: cover;
  background-position: center;
}

























